 .meandblog li a:hover { color: #000 }
 /*money*/
 .dasbox { width: 130px; float: left; margin-left: 40px; background: url(data:image/jpeg;base64,) no-repeat left 20px center #E2523A; -webkit-box-shadow: 1px 2px 6px 0px rgba(0,0,0,.2); box-shadow: 1px 2px 6px 0px rgba(0,0,0,.2); border-radius: 3px; line-height: 40px; padding-left: 10px; text-align: center; }
 .hide_box { z-index: 999; filter: alpha(opacity=50); background: #666; opacity: 0.5; -moz-opacity: 0.5; left: 0; top: 0; height: 99%; width: 100%; position: fixed;}
 .shang_box { width: 430px; padding: 40px 10px; background: #fff url(data:image/jpeg;base64,) no-repeat left top 30px; ; border-radius: 10px; position: fixed; z-index: 1000; left: 50%; top: 50%; margin-left: -280px; margin-top: -280px; border: 1px dotted #dedede; }
 .shang_box img { border: none; border-width: 0; }
 .dasbox a { color: #fff; }
 .shang_close { float: right; display: inline-block; }
 .shang_tit { width: 100%; height: 70px; text-align: center; line-height: 70px; }
 .shang_tit p { color: #a3a3a3; text-align: center; font-size: 16px; }
 .shang_payimg { width: 140px; padding: 10px; border: 6px solid #EA5F00; margin: 0 auto; border-radius: 3px; height: 140px; }
 .shang_payimg img { display: block; text-align: center; width: 140px; height: 140px; }
 .pay_explain { text-align: center; margin: 10px auto; font-size: 12px; color: #545454; }
 .radiobox { width: 16px; height: 16px; background: url(data:image/jpeg;base64,); display: block; float: left; margin-top: 5px; margin-right: 14px; }
 .checked .radiobox { background: url(data:image/jpeg;base64,); }
 .shang_payselect { text-align: center; margin: 0 auto; margin-top: 40px; cursor: pointer; height: 60px; width: 280px; }
 .shang_payselect .pay_item { display: inline-block; margin-right: 10px; float: left; }
 .shang_payselect img { float: left }
